import React from 'react';
import { useSelector } from 'react-redux';
import Wrap from 'components/wrap';
import { Header2 } from 'components/typography';
import useStyles from './use-styles';
import Item from './item';

function Reasons() {
	const styles = useStyles();
	const { data } = useSelector((state) => state.JoinInfo);

	const reasons = data.reasons.map((item, index) => {
		return {
			icon: index,
			title: item.heading,
			description: item.info,
		};
	});

	[
		{
			icon: 0,
			title: 'Remote work',
			description: 'You can work from any place\non Earth',
		},
		{
			icon: 1,
			title: 'Transparency',
			description:
				'Our employees always know what’s happening and how business is going',
		},
		{
			icon: 2,
			title: 'No bureaucracy',
			description: 'We don’t hold unnecessary meeting or do useless paperwork',
		},
		{
			icon: 3,
			title: 'Learning',
			description:
				'We encourage employees to learn and become better every day and compensate learning expenses',
		},
		{
			icon: 4,
			title: 'Flexibility',
			description:
				'We don’t follow standard\n9 to 5 schedule and offer our employees perfect work-life balance',
		},
	];
	return (
		<div className={styles.reasonsWrap}>
			<Wrap>
				<Header2 title='5 reasons to join TGA' />
				<div className={styles.reasonsRow}>
					<div className={styles.firstRow}>
						{data.reasons.map((item, index) => {
							return index <= 2 && <Item {...item} />;
						})}
					</div>
					<div className={styles.centeredRow}>
						{reasons.map((item, index) => {
							return index > 2 && <Item {...item} />;
						})}
					</div>
				</div>
			</Wrap>
		</div>
	);
}

export default Reasons;
